import { Book } from "@/types";
import axios from "axios";
import { BookEntry } from "../components/book";
import SearchBlock from "@/app/components/filters/search"
import { searchBooksBySubtopic } from "../components/book_entry";

export default async function Home() {

    let booksList : string = "";
    let newList : string = "";

        await axios
        .get('http://localhost:3000/api/books', {responseType : "json"})
        //use the form response URL mods here???
        .then(function (response) {
            booksList = response.data;
        })
        .catch((err) => console.log("couldn't read db"));

        let tag : string = ("music")
        await searchBooksBySubtopic(tag)
            .then(function (response) {
                booksList = response;
            })
            booksList = JSON.parse(newList)


    let bookArray = booksList.map((book : Book) => {
        // map is highlighted as an error here but it's working anyway?
        return (
            <BookEntry key={book.ISBN} title={book.Title} author={book.Author} isbn={book.ISBN} bookDesc={book.Description} />
        )
    })

    return (
        <div className="flex space-x-10">
            <SearchBlock />
            <div className = "flex w-48" />
            <div className = "flex w-fit">
                <div className="right-0 grid grid-cols-2 p-5 gap-5">
                    { bookArray }
                </div>  
            </div>         
        </div>             
    );
};